.dropdown__block{
   margin-top: 5rem;
   margin-bottom: 5rem;
   h1{
      margin-bottom: 3.34rem;
   }
   .dropdown__block__inner{
      h4{
         color: #000;
         font-size: 2.25rem;
         font-weight: 700;
         line-height: normal;
         margin-bottom: 2.44rem;
      }
   }
   .dropdown__menu__wrapper{
      margin-right: 1.87rem;
      padding: 1.94rem 2.69rem;
      border-radius: 0.1875rem;
      background: #FFF;
      box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.10);
      .dropdown__menu{
         ul{
            li{
               a{
                  color: #404040;
                  font-size: 1.75rem;
                  font-weight: 700;
                  line-height: 100%; 
                  transition: color .25s ease-in-out;
                  &:hover{
                     color: #999;
                  } 
               }
               position: relative;
               margin-bottom: 1.88rem;
               
               ul{
                  margin: 2rem 0 !important;
                  li{
                     margin-bottom: 15px;
                     a{
                        color: #4E4E4E;
                        font-size: 1.5rem;
                        font-weight: 400;
                        line-height: 100%;
                        transition: color .25s ease-in-out;
                        &:hover{
                           color: #999;
                        }
                     }
                     margin-left: 35px;
                     position: relative;
                     &::before{
                        content: '';
                        position: absolute;
                        left: -35px;
                        top: 12px;
                        width: 1rem;
                        height: 0.25rem;
                        background: #E7AD03;
                     }
                  }
               }
            }
         }
      }
   }
   .dropdown__links__wrapper{
      padding: 1.94rem 2.69rem;
      border-radius: 0.1875rem;
      background: #FFF;
      box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.10);
      .dropdown__links__inner_top{
         display: flex;
         justify-content: flex-start;
         flex-wrap: wrap;
         .item{
            flex-basis: 18%;
            margin-bottom: 3.63rem;
            margin-right: 5rem;

            a{
               display: block;
               &:hover{
                  p{
                     color: #999;
                  }
               }
               p{
                  margin-top: 0.98rem;
                  color: #404040;
                  font-size: 1.5rem;
                  font-weight: 700;
                  line-height: 100%;
                  transition: color .25s ease-in-out;
               }
            }
         }
      }
      .dropdown__links__inner_bottom{
         display: flex;
         align-items: center;
         margin-top: 1.75rem;
         .item{
            margin-right: 6rem;
            a{
               display: flex;
               align-items: center;
               &:hover{
                  p{
                     color: #999;
                  }
               } 
            }
            img{
               margin-right: 10px;
            }
            p{
               color: #000;
               font-size: 1.5rem;
               font-weight: 400;
               transition: color .25s ease-in-out;
            }
         }
      }
   }
}
.carts__wrapper{
   margin-bottom: 8.87rem;
   .title{
      margin-bottom: 3.75rem;
   }
}
.carts__inner{
   gap: 2rem;
}
.cart{
   flex-basis: 22%;
   a{
      display: block;
      &:hover{
         h4{
            color: #36b5ff;
         }
      }
   }
   position: relative;
   padding: 1.77rem 1.77rem 4.44rem;
   border-radius: 0.3125rem;
   background: #FFF;
   box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.10);
   .label-new{
      position: absolute;
      top: 0;
      left: 1.75rem;
      display: inline-flex;
      padding: 0.3125rem 0.625rem;
      background: #00BB4B;
      color: #FFF;
      font-size: 1.125rem;
      font-weight: 700;
      line-height: 100%;
      text-transform: uppercase;
   }
   .label-weight{
      position: absolute;
      bottom: 0;
      left: 1.75rem;
      display: inline-flex;
      padding: 0.3125rem 0.625rem;
      background: #D9D9D9;
      color: #000;
      font-size: 1.125rem;
      font-weight: 700;
      line-height: normal;
   }
   img{
      width: 100%;
   }
   h4{
      color: #0085D0;
      font-size: 1.5rem;
      font-weight: 700;
      line-height: normal;
      margin-bottom: 1.56rem;
      transition: color .25s ease-in-out;
   }
   
}
.row__images__wrapper{
   margin-bottom: 7.19rem;
   .row__images__inner{
      .row__images__item{
         a{
            display: block;
         }
         height: 18.5625rem;
         margin-right: 1rem;
         background-color: #D9D9D9;
         overflow: hidden;
         padding: 2rem;
         position: relative;
         p{
            display: block;
            position: absolute;
            width: 50%;
            height: 100%;
            top: 2rem;
            left: 2rem;
            color: #FFF;
            font-size: 1.75rem;
            font-weight: 700;
            line-height: normal;
         }
         &:before{
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 0;
            height: 0;
            border: 0 solid transparent;
            border-left-width: 0px;
            border-right-width: 30.0625rem;
         }
         
      }
      .grey-rectangel::before{
         border-top: 32.5625rem solid rgba(80, 80, 80, 0.33);
      }
      .yellow-rectangel::before{
         border-top: 32.5625rem solid rgba(255, 170, 0, 0.487);
      }
      .black-rectangel::before{
         border-top: 32.5625rem solid rgba(0, 0, 0, 0.48);
      }
   }
}