.tab-wrapper {
   display: block;
   margin: 0 auto;
 }
 
 .tabs {
   margin: 0;
   padding: 0;
   display: flex;
 }
 
 .tab-link {
   &:nth-last-child(1){
      margin-right: 0;
   }
   margin-right: 3.88rem;
   color: #404040;
   font-size: 1.75rem;
   font-weight: 700;
   cursor: pointer;
   border-bottom: 0.375rem solid transparent;
 }
 
 .tab-link:hover {
   color: #999;
 }
 
 .tab-link.active {
   color: #000;
   border-color: #E7AD03;
 }
 
 .content-wrapper {
   padding: 3.81rem 0;
 }
 
 .tab-content {
   display: none;
   opacity: 0;
   transform: translateY(15px);
   animation: fadeIn 0.25s ease 1 forwards;
   ul{
      margin-bottom: 4.25rem !important;
      li{
         color: #000;
         font-size: 1.5rem;
         font-weight: 400;
         line-height: 120%; 
         margin-left: 1.2rem;
         margin-bottom: 0.9rem;
         position: relative;
         &::before{
            content: '';
            position: absolute;
            left: -1.2rem;
            top: 12px;
            width: 5px;
            height: 5px;
            background: #000;
            border-radius: 50%;
         }
      }
   }
   .button{
      a{
         padding: 1.25rem 1.875rem;
         font-size: 1.25rem;
      }
   }
 }
 
 .tab-content.active {
   display: block;
 }
 
 @keyframes fadeIn {
   100% {
     opacity: 1;
     transform: none;
   }
 }