*,
*::before,
*::after {
	box-sizing: border-box;
}
html,
body {
	min-height: 100%;
	position: relative;
}

body {
	font-size: 1rem;
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
}
h1{
	color: #000;
	font-size: 5rem;
	font-weight: 700;
	line-height: 100%;
}
h2{
	color: #000;
	font-size: 3.5rem;
	font-weight: 700;
	line-height: 100%;
}
h3{
	color: #000;
	font-size: 2.25rem;
	font-family: Gilroy;
	font-style: normal;
	font-weight: 700;
	line-height: 130%;
}
h4{
	color: #404040;
	font-size: 1.75rem;
	font-weight: 700;
}
p.lead{
	color: #000;
	font-size: 1.75rem;
	font-weight: 400;
	line-height: 120%;
}
p{
	color: #000;
	font-size: 1.5rem;
	font-weight: 400;
	line-height: normal;
}
a{
	text-decoration: none !important;
}

ul{
	list-style-type: none;
	margin: 0 !important;
	padding: 0 !important;
}
.title{
	color: #000;
	font-size: 3.5rem;
	font-weight: 700;
	line-height: normal;
}
.title-lg{
	color: #000;
	font-size: 5rem;
	font-weight: 700;
	line-height: 100%;
}
.swiper-button-next::after{
	content: url('../img/svg/next.svg') !important;
}
.swiper-button-prev::after{
	content: url('../img/svg/prev.svg') !important;
}
.btn-dark{
	background: #404040 !important;
	color: #FFF !important;
	font-size: 0.875rem !important;
	font-weight: 700 !important;
	line-height: 130% !important;
	padding: 0.6875rem 0.9375rem !important;
	&:hover{
		border-color: #999 !important;
		background: #999 !important;
	}
}
.btn-dark-lg{
	background: #404040 !important;
	color: #FFF !important;
	font-size: 1.25rem !important;
	font-weight: 700 !important;
	line-height: 130% !important;
	padding: 1.25rem 1.875rem !important;
	&:hover{
		border-color: #999 !important;
		background: #999 !important;
	}
}
.btn-warning{
	padding: 15px 70px !important;
	font-weight: 700 !important;
	border-radius: 0 !important;
	&:hover{
		background: #FFCC37 !important;
		border-color: transparent !important;
	}
}

.toggler{
	position: absolute;
	right: 0;
	top: 6px;
	width: 1.25rem;
	height: 1.25rem;
	background-image: url(../img/svg/arrow-down.svg);
}
.toggler.open{
	transform: rotate(180deg);
}

.list{
	li{
		position: relative;
		margin-left: 1rem;
		color: #000;
		font-size: 1.25rem;
		font-weight: 400;
		&:before{
			content: '';
			position: absolute;
			width: 5px;
			height: 5px;
			left: -1rem;
			top: 12px;
			border-radius: 50%;
			background: #000;
		}
	}
}
.dropdown__list{
	position: relative;
	a{
		transition: background .25s !important;
	}
	&:hover{
		a{
			background: #D2D2D2;
		}
		ul{
			transform: translateY(0);
			opacity: 1;
			display: block;
		}
	}
	ul{
		z-index: 999;
		width: 100%;
		padding-top: 2rem !important;
		padding-left: 1rem !important;
		position: absolute;
		background: #E9E9E9;
		top: 49px;
		left: 0;
		opacity: 0;
		transform: translateY(-200%);
		li{
			a{
				padding: 0 !important;
				background-color: transparent !important;
				color: #404040;
				font-size: 1.25rem !important;
				font-weight: 700;
				line-height: 100%;
				text-transform: none !important;
				transition: color .25s ease-in-out;
				&:hover{
					color: #999;
				} 
			}
			position: relative;
			margin-bottom: 1.88rem;
			
		}
	}
}