footer{
   background: #222;
   padding-top: 2.5rem;
   padding-bottom: 4.12rem;
   .footer__inner{
      .footer__logo{
         img{
            transform: translateY(-2.5rem) !important;
            margin-bottom: -1.7rem;
         }
         p{
            color: #FFF;
            font-size: 1.25rem;
            font-weight: 400;
            margin-bottom: 9.35rem !important;
         }
         a{
            color: #B4B4B4;
            font-size: 1.25rem;
            font-weight: 400;
         }
      }
      .footer__nav{
         li{
            margin-bottom: 0.56rem;
            a{
               color: #FFF;
               font-size: 1.25rem;
               font-weight: 400;
               transition: color .25s ease-in-out;
               &:hover{
                  color:#B4B4B4
               }
            }
         }
      }
      .footer__addres{
         div{
            margin-bottom: 2.13rem;
         }
         h6{
            color: #FFF;
            font-size: 1.25rem;
            font-weight: 700;
         }
         p{
            color: #FFF;
            font-size: 1.25rem;
            font-weight: 400;
         }
      }
      .footer__contact{
         .phone{
            a{
               color: #FFF;
               font-size: 2.25rem;
               font-weight: 400;
            }
            p{
               color: #FFF;
               font-size: 1.25rem;
               font-weight: 400;
            }
         }
         .footer__links__wrapper{
            display: block;
            a{
               color: #FFF;
               font-size: 1.25rem;
               font-weight: 400;
               position: relative;
               margin-left: 2em;
               margin-bottom: 13px;
               &:nth-child(1){
                  &::after{
                     content: url('../img/svg/whats-app.svg');
                     position: absolute;
                     width: 24px;
                     height: 24px;
                     left: -2.25rem;
                     top: 0;
                  }
               }
               &:nth-last-child(1){
                  &::after{
                     content: url('../img/svg/tg-small.svg');
                     position: absolute;
                     width: 24px;
                     height: 24px;
                     left: -2.25rem;
                     top: 0;
                  }
               }
            }
         }
      }
      .footer__social{
         a{
            color: #FFF;
            font-size: 20px;
            font-weight: 400;
         }
         .footer__img_link__wrapper{
            img{
               margin-right: 5px;
               &:nth-last-child(1){
                  margin-left: 0;
               }
            }
         }
      }
   }
}