.top__slider__block{
   margin-bottom: 5.25em;
   border-bottom: 1px solid #E7AD03;

   .top__slider{
      padding-top: 2rem;
      .slider__btns{
         position: absolute;
         right: 0;
         bottom: 20px;
         height: auto;
         max-width: 150px !important;
         .swiper-button-next, .swiper-button-prev{
            position: relative !important;
         }
         .swiper-button-prev{
            left: -38px !important;
         }
         
      }
      .swiper-slide_left{
         h1{
            color: #404040;
            font-size: 11.25rem;
            font-weight: 700;
            line-height: 100%;
            text-transform: uppercase;
            margin-bottom: 2rem;
         }
         h4{
            color: #222;
            font-size: 3.5rem;
            font-weight: 400;
            line-height: 100%;
            margin-bottom: 1.5rem;
         }
         p{
            color: #000;
            font-size: 1.75rem;
            font-weight: 400;
            line-height: 120%;
         }
      }
      .swiper-slide_right{
         background-size: contain;
         background-position: left center;
         background-repeat: no-repeat;
         min-height: 496px;
      }
   }
}
.rows__block{
   margin-bottom: 6.76rem;
   .rows__block__wrapper{
      .item{
         p{
            color: #000;
            font-size: 1.5rem;
            font-weight: 400;
            line-height: normal;
         }
         .mr-15{
            margin-right: 15px;
         }
      }
   }
}
.products__demo{
   margin-bottom: 6.69rem;
   .title{
      margin-bottom: 1rem;
   }
   .products__wrapper{
      .demo__left{
         flex-basis: 50%;
         max-width: 50%;
         .swiper-slide{
            .slide__info{
               a{
                  cursor: pointer;
                  color: #404040;
                  font-size: 1.75rem;
                  font-weight: 700;
                  transition: color .25s ease-in-out;
                  &:hover{
                     color: #999;
                  }
               }
               .weight{
                  color: #000;
                  font-size: 1.125rem;
                  font-weight: 700;
               }
               .desc{
                  color: #000;
                  font-size: 1.125rem;
                  font-weight: 400;
               }
               ul{
                  margin-bottom: 3rem !important;
                  li{
                     color: #000;
                     font-size: 1.125rem;
                     font-family: Gilroy;
                     font-style: normal;
                     font-weight: 400;
                     position: relative;
                     margin-left: 2rem;
                     &::after{
                        content: url('../../img/svg/done.svg');
                        position: absolute;
                        width: 1.03125rem;
                        height: 0.65625rem;
                        left: -2rem;
                        top: 0;
                     }
                  }
               }
               .button{
                  a{
                     padding: 0.625rem 1.25rem !important;
                  }
               }
            }
         }
         .produrct__slider__btns{
            max-width: 130px !important;
            position: absolute;
            right: 0;
            bottom: 35px;
            height: auto;
            display: flex;
            justify-content: space-between;
            .swiper-button-next, .swiper-button-prev{
               position: relative !important;
            }
            .swiper-button-next::after{
               content: url('../img/svg/next2.svg') !important;
            }
            .swiper-button-prev::after{
               content: url('../img/svg/prev2.svg') !important;
            }
            .swiper-button-prev{
               left: -25px;
            }
         }
      }
      .demo__right{
         position: relative;
         margin-top: 1rem;
         margin-left: 6.25rem;
         flex-basis: 50%;
         display: grid;
         grid-template-columns: repeat(3,1fr);
         grid-template-rows: repeat(2, 1fr);
         .item{
            a{
               display: block;
            }
            text-align: center;
            h5{
               margin-top: 0.75rem;
               color: #404040;
               text-align: center;
               font-size: 1.125rem;
               font-weight: 700;
               text-transform: uppercase;
            }
         }
         &::before{
            content: url('../img/svg/line.svg');
            position: absolute;
            left: -4.25rem;
            top: 0;
            width: 100px;
            height: 100px;
         }
      }
   }
   .border__block{
      margin-top: 4.84rem;
      border: 2px solid #E7AD03;
      background: #FFF;
      padding: 2.94rem 3.56rem;
      h3{
         flex-basis: 22%;
         color: #000;
         font-size: 2.25rem;
         font-weight: 700;
         line-height: none;
         margin-bottom: 0;
      }
      ul{
         flex-basis: 21%;
         li{
            margin-bottom: 1.88rem;
            a{
               color: #404040;
               font-size: 1.45rem;
               font-weight: 700;
               line-height: 100%;
               transition: color .25s ease-in-out;
               &:hover{
                  color: #999;
               }
            }
            &:nth-last-child(1){
               margin-bottom: 0;
            }
         }
         &:nth-last-child(1){
            flex-basis: 30%;
         }
      }
   }
}

.service{
   background: url(../img/svg/service_bg.svg), #F5F5F5 0px -68.649px / 100% 100.179% no-repeat;
   background-repeat: no-repeat;
   background-size: cover;
   background-position: 450px 100%;
   padding-bottom: 5rem;
   padding-top: 2.88rem;
   position: relative;
   &::after{
      content: url('../img/right__img.webp');
      position: absolute;
      right: 100px;
      top: 50px;
      height: 28.8125rem;
      filter: saturate(200%); 
      transform: scale(1.2);
   }
   .title{
      margin-bottom: 1.94rem;
   }
   .button{
      z-index: 999;
   }
   .service_block{
      max-width: 73rem;
      z-index: 999;
      background: #FFF;
      box-shadow: 0px 0px 15px 2px rgba(0, 0, 0, 0.15);
      display: grid;
      grid-template-columns: repeat(2,1fr);
      .item{
         padding: 1.5rem;
         img{
            width: 10rem;
            height: 10rem;
            margin-right: 1.56rem;
         }
         .text{
            color: #000;
            font-size: 1.75rem;
            font-weight: 400;
         }
         a{
            cursor: pointer;
            &:hover{
               h6{
                  color: #999;
               }
            }
         }
         h6{
            color: #404040;
            font-size: 1.75rem;
            font-weight: 700;
            line-height: 120%;
            transition: color .25s ease-in-out;
            
         }
         div p{
            color: #000;
            font-size: 1.125rem;
            font-weight: 400;
         }
      }
   }
}
.tabs__block{
   margin-top: 4.38rem;
   margin-bottom: 7rem;
   .subtitle{
      color: #000;
      font-size: 2rem;
      font-weight: 400;
      line-height: 120%;
   }
   .tabs__block__inner{
      margin-top: 3.34rem;
   }
   .tabs__block__left{
      .item{
         &:nth-child(1){
            margin-right: 1.87rem;
         }
         ul{
            li{
               color: #000;
               font-size: 1.125rem;
               font-weight: 400;
               line-height: 120%; 
               margin-left: 1.2rem;
               margin-bottom: 0.3rem;
               position: relative;
               &::before{
                  content: '';
                  position: absolute;
                  left: -1.2rem;
                  top: 7px;
                  width: 5px;
                  height: 5px;
                  background: #000;
                  border-radius: 50%;
               }
            }
         }
         .image{
            margin-bottom: 1.81rem;
            .sub__image{
               position: relative;
               background-color: #E7AD03;
               padding: 0.63rem 1.25rem;
               h6{
                  color: #000;
                  font-size: 1.5rem;
                  font-weight: 700;
               }
               &::before{
                  content: '';
                  position: absolute;
                  right: 0;
                  top: 0;
                  width: 0;
                  height: 0;
                  border: 0 solid transparent;
                  border-right-width: 0px;
                  border-left-width: 4rem;
                  border-bottom: 4rem solid rgb(255, 255, 255);
               }
            }
         }
      }
   }
   .tabs__block__right{
      width: 35%;
      margin-left: 6.25rem;
      position: relative;
      &::before{
         content: url('../img/svg/line.svg');
         position: absolute;
         left: -10.25rem;
         top: 0;
         width: 100px;
         height: 100px;
      }
   }
}

