   background-position: center;
   .about__content__left{
      &_top{
         max-width: 37.70206rem;
         background: #fff;
         padding: 4.77rem 3.63rem 4.35rem;
         h2{
            color: #000;
            font-size: 4.375rem;
            font-weight: 700;
            line-height: 100%;
            margin-bottom: 4.33rem;
         }
         p{
            color: #000;
            font-size: 1.75rem;
            font-weight: 400;
            line-height: 120%;
         }
