.our_projects__wrapper{
   margin-top: 4.81rem;
   margin-bottom: 2rem;
   .projects__inner{
      margin-top: 1.87rem;
      .projects__inner_item{
         a{
            display: block;
            height: 372.448px;
            position: relative;
            p{
               max-width: 72%;
               position: absolute;
               bottom: 0;
               left: 0;
               color: #FFF;
               line-height: 30px;
               font-size: 1.75rem;
               font-weight: 700;
               margin-left: 2rem;
               margin-bottom: 1.65rem;
            }
            &::before{
               background: linear-gradient(0deg, rgba(0, 0, 0, 0.6) 0%, rgba(0, 0, 0, 0) 100%);
