.header{
   padding-top: 2.75em;
   background: #F7F7F7;
   .header__top{
      display: flex;
      justify-content: space-between;
      align-items: center;
      .logo{
         transform: translateY(-2.75rem);
         flex-basis: 10%;
      }
      .text{
         font-size: 1.125rem;
         font-weight: 400;
         line-height: 22px;
         color: #999;
         flex-basis: 20%;
      }
      .phone{
         flex-basis: 20%;
         text-align: left;
         a{
            color: #000;
            font-size: 1.75rem;
            line-height: 1;
            font-weight: 400;
         }
         p{
            color: #999;
            font-size: 1rem;
            font-weight: 400;
         }
      }
      .button{
         flex-basis: 13%;
         margin-top: .6875rem;
      }
      .calc a, .acc a{
         flex-basis: 15%;
         div{
            margin-right: 0.88rem;
            margin-top: 2px;
         }
         p{
            color: #404040;
            font-size: 1rem;
            font-weight: 400;
            line-height: 20px;
         }
      }
   }
}
.header__nav{
   padding-bottom: 20px;
   ul{
      li{
         
         a{
            color: #404040;
            font-size: 1.25rem;
            font-weight: 700;
            text-transform: uppercase;
            transition: color .25s ease-in-out;
            padding: 25px;
            &:hover{
               color: #999;
            }
         }
      }
   }
}